import { DashboardData, UserProfile } from '@/types/dashboard';

export class ApiError extends Error {
  constructor(public status: number, message: string) {
    super(message);
    this.name = 'ApiError';
  }
}

export const api = {
  async validateToken(token: string): Promise<UserProfile> {
    const response = await fetch('https://scope-fastapi-194s.onrender.com/api/auth/validate', {
      headers: {
        'Authorization': `Bearer ${token}`
      }
    });

    if (!response.ok) {
      throw new ApiError(response.status, 'Invalid token');
    }

    const data = await response.json();
    if (!data.success) {
      throw new ApiError(401, data.error || 'Invalid token');
    }

    return data.user;
  },

  async fetchDashboardData(): Promise<DashboardData> {
    const response = await fetch('https://scope-fastapi-194s.onrender.com/api/data.json');
    
    if (!response.ok) {
      throw new ApiError(response.status, 'Failed to fetch dashboard data');
    }

    return response.json();
  },

  async checkAuthAndGetData(): Promise<{
    isAuthenticated: boolean;
    profile?: UserProfile;
    dashboardData?: DashboardData;
  }> {
    const token = localStorage.getItem('jwt_token');
    
    if (!token) {
      return { isAuthenticated: false };
    }

    try {
      // Run both requests in parallel
      const [profile, dashboardData] = await Promise.all([
        this.validateToken(token),
        this.fetchDashboardData()
      ]);

      return { 
        isAuthenticated: true, 
        profile, 
        dashboardData 
      };
    } catch (error) {
      // If there's an auth error, clear the token
      if (error instanceof ApiError && error.status === 401) {
        localStorage.removeItem('jwt_token');
        return { isAuthenticated: false };
      }
      throw error; // Re-throw other errors
    }
  }
};